h1 {
  font-style: normal;
  font-weight: bold;
  font-size: 36px;
  line-height: 41px;
}

h2 {
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 48px;
}

p {
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 41px;
}

a {
  color: black;
  text-decoration: none;
}

/* Index Navigation */

.lineTwo {
  margin-top: 140px;
  margin-left: 250px;
}

.lineThree {
  margin-top: 200px;
  margin-left: 50px;
}

/* hover colours */
a#soundtracks:hover {
  color: #e5989b;
}

a#theworldaroundsofa:hover {
  color: #06d6a0;
}

a#aboutartist:hover {
  color: #56cfe1;
}

a#sofaproject:hover {
  color: #ffe66d;
}

a#recommendations:hover {
  color: #f94144;
}

.theworldaroundsofa {
  background-color: #06d6a0 !important;
}

